.cooperation-partner-wrapper {
  grid-area: cooperation-partner;
  display: flex;
    .partner-title {
      margin-top: 15px;
        width: 70px;
        line-height: 50px;
        font-size: 16px;
        color: gray;
    }
    .partner-list {
        li {
            display: inline-block;
            margin-left: 11px;
            border: 1px solid gray;
            list-style: none;
            img {
                width: 76px;
            }
        }  
    }
}
